@mixin wrapper {
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;

  @media (min-width: $tablet-width) {
    width: 768px;
    margin: 0 auto;
    padding: 0 30px;
  }

  @media (min-width: $desktop-width) {
    width: 1300px;
    margin: 0 auto;
    padding: 0 40px;
  }
}
